body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Принудительное box-sizing для точного контроля размеров */
*, *::before, *::after {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Базовые утилиты вместо Tailwind */
.flex { display: flex; }
.flex-col { flex-direction: column; }
.flex-1 { flex: 1; }
.items-center { align-items: center; }
.justify-between { justify-content: space-between; }
.justify-center { justify-content: center; }
.gap-2 { gap: 8px; }
.gap-3 { gap: 12px; }
.gap-4 { gap: 16px; }

.h-screen { height: 100vh; }
.h-full { height: 100%; }
.h-12 { height: 48px; }
.w-20 { width: 80px; }
.w-64 { width: 256px; }

.bg-white { background-color: white; }
.bg-gray-50 { background-color: #f9fafb; }
.bg-gray-100 { background-color: #f3f4f6; }
.bg-blue-50 { background-color: #eff6ff; }
.bg-blue-600 { background-color: #2563eb; }
.bg-green-100 { background-color: #dcfce7; }
.bg-green-600 { background-color: #16a34a; }
.bg-orange-100 { background-color: #fed7aa; }
.bg-red-50 { background-color: #fef2f2; }
.bg-red-100 { background-color: #fee2e2; }
.bg-red-600 { background-color: #dc2626; }
.bg-red-500 { background-color: #ef4444; }

.text-white { color: white; }
.text-gray-600 { color: #4b5563; }
.text-gray-900 { color: #111827; }
.text-blue-600 { color: #2563eb; }
.text-green-900 { color: #14532d; }
.text-orange-900 { color: #9a3412; }
.text-red-700 { color: #b91c1c; }
.text-red-900 { color: #7f1d1d; }

.text-xs { font-size: 12px; }
.text-sm { font-size: 14px; }
.text-lg { font-size: 18px; }
.text-xl { font-size: 20px; }

.font-medium { font-weight: 500; }
.font-semibold { font-weight: 600; }

.p-2 { padding: 8px; }
.p-3 { padding: 12px; }
.px-1 { padding-left: 4px; padding-right: 4px; }
.px-4 { padding-left: 16px; padding-right: 16px; }
.py-2 { padding-top: 8px; padding-bottom: 8px; }
.py-3 { padding-top: 12px; padding-bottom: 12px; }
.py-0\.5 { padding-top: 2px; padding-bottom: 2px; }

.mt-1 { margin-top: 4px; }
.mt-2 { margin-top: 8px; }
.mt-4 { margin-top: 16px; }
.mb-2 { margin-bottom: 8px; }
.ml-1 { margin-left: 4px; }
.ml-auto { margin-left: auto; }

.border { border: 1px solid #d1d5db; }
.border-b { border-bottom: 1px solid #d1d5db; }
.border-l { border-left: 1px solid #d1d5db; }
.border-r { border-right: 1px solid #d1d5db; }
.border-t { border-top: 1px solid #d1d5db; }
.border-gray-200 { border-color: #e5e7eb; }
.border-gray-300 { border-color: #d1d5db; }
.border-blue-200 { border-color: #bfdbfe; }
.border-blue-400 { border-color: #60a5fa; }
.border-green-400 { border-color: #4ade80; }
.border-orange-400 { border-color: #fb923c; }

.rounded { border-radius: 6px; }
.shadow-sm { box-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05); }
.shadow-md { box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1); }

.overflow-hidden { overflow: hidden; }
.overflow-auto { overflow: auto; }
.overflow-x-auto { overflow-x: auto; }
.overflow-y-auto { overflow-y: auto; }
.overflow-y-hidden { overflow-y: hidden; }

.relative { position: relative; }
.absolute { position: absolute; }
.top-0 { top: 0; }
.top-1 { top: 4px; }
.bottom-0 { bottom: 0; }
.bottom-1 { bottom: 4px; }
.left-0 { left: 0; }
.right-0 { right: 0; }

.space-y-1 > * + * { margin-top: 4px; }

.z-10 { z-index: 10; }
.z-20 { z-index: 20; }

.cursor-pointer { cursor: pointer; }
.truncate { 
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.hover\:bg-gray-50:hover { background-color: #f9fafb; }
.hover\:bg-blue-700:hover { background-color: #1d4ed8; }
.hover\:bg-green-700:hover { background-color: #15803d; }
.hover\:bg-red-700:hover { background-color: #b91c1c; }
.hover\:shadow-md:hover { box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1); }

.disabled\:opacity-50:disabled { opacity: 0.5; }

.transition-shadow { transition: box-shadow 0.15s ease-in-out; }

.opacity-75 { opacity: 0.75; }

.w-0\.5 { width: 2px; }

.hidden { display: none; }
